.product {
  max-width: 200px;
  border: 1px solid $borderColort;
  border-radius: 5px;
  padding: 15px 14px;

  &__image {
    height: 164px;
    width: 166px;
    padding: 12px 0;
    margin: 0 auto 30px;
    border: 3px solid transparent;
    border-radius: 5px;
    transition: 400ms linear;
    &:hover {
      transform: scale(1.2);
      border-color: $borderColort;
    }
  }

  &__photo {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }

  &__name {
    @include font($fontFamily, 12px, 15px, 500);
    margin-bottom: 5px;
  }

  &__code {
    display: block;
    @include font($fontFamily, 11px, 13px, normal);
  }

  &__reviews {
    display: grid;
    grid-template-columns: repeat(2, auto);
    justify-content: space-between;
    margin-top: 15px;
    margin-bottom: 20px;
  }

  &__feedback {
    @include font($fontFamily, 11px, 13px, normal);
  }

  &__price {
    display: grid;
    grid-template-columns: repeat(2, auto);
    justify-content: space-between;
    align-items: baseline;
    margin-bottom: 20px;
  }

  &__text {
    @include font($fontFamily, 11px, 13px, normal);
  }

  &__num {
    font-size: 16px;
    line-height: 19px;
    @include font($fontFamily, 16px, 19px, 700);
    color: #000;
  }
  .product__bay {
    display: block;
    padding: 10px 10px 11px;
    @include font($fontFamily, 14px, 16px, 700);
    min-height: 40px;
    text-align: center;
    text-transform: uppercase;
    text-decoration: none;
    color: $backgroudColor;
    background-color: $colorLightBlue;
    border: 1px solid $colorLightBlue;
    border-radius: 5px;
    transition: color 300ms, background-color 300ms ;

    &:hover {
      background-color: $backgroudColor;
      color: $colorLightBlue;
    }
  }
}
